// Advanced performance optimizations specifically for Admin Panel
import React, { useMemo, useCallback, useRef, useEffect } from 'react';
// Simple utility functions to replace deleted performanceUtils
const debounce = (func, wait) => {
  let timeout;
  return function executedFunction(...args) {
    const later = () => {
      clearTimeout(timeout);
      func(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
  };
};

const throttle = (func, limit) => {
  let inThrottle;
  return function() {
    const args = arguments;
    const context = this;
    if (!inThrottle) {
      func.apply(context, args);
      inThrottle = true;
      setTimeout(() => inThrottle = false, limit);
    }
  };
};

const apiCache = new Map();

// Optimized data sync manager for background updates
export class AdminDataSyncManager {
  constructor() {
    this.subscriptions = new Map();
    this.syncIntervals = new Map();
    this.isVisible = true;
    this.setupVisibilityListener();
  }

  setupVisibilityListener() {
    document.addEventListener('visibilitychange', () => {
      this.isVisible = !document.hidden;
      if (this.isVisible) {
        // Resume syncing when tab becomes visible
        this.resumeAllSyncs();
      } else {
        // Pause syncing when tab is hidden
        this.pauseAllSyncs();
      }
    });
  }

  subscribe(key, syncFunction, interval = 30000) {
    this.subscriptions.set(key, syncFunction);
    
    if (this.isVisible) {
      const intervalId = setInterval(async () => {
        if (this.isVisible) {
          try {
            await syncFunction();
          } catch (error) {
            console.error(`Background sync failed for ${key}:`, error);
          }
        }
      }, interval);
      
      this.syncIntervals.set(key, intervalId);
    }
  }

  unsubscribe(key) {
    const intervalId = this.syncIntervals.get(key);
    if (intervalId) {
      clearInterval(intervalId);
      this.syncIntervals.delete(key);
    }
    this.subscriptions.delete(key);
  }

  pauseAllSyncs() {
    this.syncIntervals.forEach((intervalId) => {
      clearInterval(intervalId);
    });
    this.syncIntervals.clear();
  }

  resumeAllSyncs() {
    this.subscriptions.forEach((syncFunction, key) => {
      const intervalId = setInterval(async () => {
        if (this.isVisible) {
          try {
            await syncFunction();
          } catch (error) {
            console.error(`Background sync failed for ${key}:`, error);
          }
        }
      }, 30000);
      
      this.syncIntervals.set(key, intervalId);
    });
  }

  destroy() {
    this.pauseAllSyncs();
    this.subscriptions.clear();
  }
}

// Global sync manager instance
export const adminSyncManager = new AdminDataSyncManager();

// Optimized state management hook with selective updates
export const useOptimizedAdminState = (initialState, compareFn = null) => {
  const [state, setState] = React.useState(initialState);
  const previousStateRef = useRef(initialState);

  const optimizedSetState = useCallback((newState) => {
    const updatedState = typeof newState === 'function' ? newState(previousStateRef.current) : newState;
    
    // Use custom compare function or deep comparison
    const hasChanged = compareFn 
      ? !compareFn(previousStateRef.current, updatedState)
      : JSON.stringify(previousStateRef.current) !== JSON.stringify(updatedState);
    
    if (hasChanged) {
      previousStateRef.current = updatedState;
      setState(updatedState);
    }
  }, [compareFn]);

  return [state, optimizedSetState];
};

// Memoized data transformation hook
export const useMemoizedTransform = (data, transformFn, dependencies = []) => {
  return useMemo(() => {
    if (!data) return null;
    return transformFn(data);
  }, [data, ...dependencies]);
};

// Background data fetcher with silent updates
export const useBackgroundDataFetcher = (fetchFn, key, interval = 30000) => {
  const [data, setData] = useOptimizedAdminState(null);
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(null);
  const isInitialLoad = useRef(true);

  const silentFetch = useCallback(async () => {
    try {
      const result = await fetchFn();
      setData(result);
      setError(null);
      
      if (isInitialLoad.current) {
        setLoading(false);
        isInitialLoad.current = false;
      }
    } catch (err) {
      setError(err);
      if (isInitialLoad.current) {
        setLoading(false);
        isInitialLoad.current = false;
      }
    }
  }, [fetchFn]);

  useEffect(() => {
    // Initial load
    silentFetch();
    
    // Subscribe to background sync
    adminSyncManager.subscribe(key, silentFetch, interval);
    
    return () => {
      adminSyncManager.unsubscribe(key);
    };
  }, [key, silentFetch, interval]);

  return { data, loading, error, refetch: silentFetch };
};

// Optimized search hook with debouncing
export const useOptimizedSearch = (data, searchFields, debounceMs = 300) => {
  const [searchTerm, setSearchTerm] = React.useState('');
  const [filteredData, setFilteredData] = React.useState(data);

  const debouncedSearch = useCallback(
    debounce((term, dataToSearch) => {
      if (!term.trim()) {
        setFilteredData(dataToSearch);
        return;
      }

      const filtered = dataToSearch.filter(item => {
        return searchFields.some(field => {
          const value = field.split('.').reduce((obj, key) => obj?.[key], item);
          return value?.toString().toLowerCase().includes(term.toLowerCase());
        });
      });

      setFilteredData(filtered);
    }, debounceMs),
    [searchFields, debounceMs]
  );

  useEffect(() => {
    debouncedSearch(searchTerm, data || []);
  }, [searchTerm, data, debouncedSearch]);

  return {
    searchTerm,
    setSearchTerm,
    filteredData
  };
};

// Virtualized list hook for large datasets
export const useVirtualizedList = (items, itemHeight = 60, containerHeight = 400) => {
  const [scrollTop, setScrollTop] = React.useState(0);
  const [containerRef, setContainerRef] = React.useState(null);

  const visibleItems = useMemo(() => {
    if (!items || items.length === 0) return [];

    const startIndex = Math.floor(scrollTop / itemHeight);
    const endIndex = Math.min(
      startIndex + Math.ceil(containerHeight / itemHeight) + 1,
      items.length
    );

    return items.slice(startIndex, endIndex).map((item, index) => ({
      ...item,
      index: startIndex + index,
      top: (startIndex + index) * itemHeight
    }));
  }, [items, scrollTop, itemHeight, containerHeight]);

  const totalHeight = (items?.length || 0) * itemHeight;

  const handleScroll = useCallback(
    throttle((e) => {
      setScrollTop(e.target.scrollTop);
    }, 16), // ~60fps
    []
  );

  return {
    visibleItems,
    totalHeight,
    containerRef: setContainerRef,
    onScroll: handleScroll
  };
};

// Performance monitoring hook for admin components
export const useAdminPerformanceMonitor = (componentName) => {
  const renderCount = useRef(0);
  const mountTime = useRef(Date.now());

  useEffect(() => {
    renderCount.current += 1;
  });

  useEffect(() => {
    const loadTime = Date.now() - mountTime.current;
    console.log(`ðŸŽ¯ ${componentName} loaded in ${loadTime}ms`);

    return () => {
      console.log(`ðŸ“Š ${componentName} rendered ${renderCount.current} times`);
    };
  }, [componentName]);

  return { renderCount: renderCount.current };
};

// Optimized modal manager
export const useOptimizedModal = () => {
  const [modals, setModals] = React.useState({});

  const openModal = useCallback((modalId, data = null) => {
    setModals(prev => ({
      ...prev,
      [modalId]: { isOpen: true, data }
    }));
  }, []);

  const closeModal = useCallback((modalId) => {
    setModals(prev => ({
      ...prev,
      [modalId]: { isOpen: false, data: null }
    }));
  }, []);

  const isModalOpen = useCallback((modalId) => {
    return modals[modalId]?.isOpen || false;
  }, [modals]);

  const getModalData = useCallback((modalId) => {
    return modals[modalId]?.data || null;
  }, [modals]);

  return {
    openModal,
    closeModal,
    isModalOpen,
    getModalData
  };
};

// Smart cache invalidation
export const useSmartCache = (keys = []) => {
  const invalidateCache = useCallback((pattern = null) => {
    if (pattern) {
      // Invalidate keys matching pattern
      keys.forEach(key => {
        if (key.includes(pattern)) {
          apiCache.delete(key);
        }
      });
    } else {
      // Invalidate all specified keys
      keys.forEach(key => apiCache.delete(key));
    }
  }, [keys]);

  const refreshCache = useCallback(async (key, fetchFn) => {
    apiCache.delete(key);
    try {
      const data = await fetchFn();
      apiCache.set(key, data);
      return data;
    } catch (error) {
      console.error(`Failed to refresh cache for ${key}:`, error);
      throw error;
    }
  }, []);

  return { invalidateCache, refreshCache };
};

// Batch operation manager
export const useBatchOperations = () => {
  const [operations, setOperations] = React.useState([]);
  const [isProcessing, setIsProcessing] = React.useState(false);

  const addOperation = useCallback((operation) => {
    setOperations(prev => [...prev, operation]);
  }, []);

  const processBatch = useCallback(async () => {
    if (operations.length === 0 || isProcessing) return;

    setIsProcessing(true);
    try {
      const results = await Promise.allSettled(
        operations.map(op => op.execute())
      );
      
      const successful = results.filter(r => r.status === 'fulfilled').length;
      const failed = results.filter(r => r.status === 'rejected').length;
      
      console.log(`Batch completed: ${successful} successful, ${failed} failed`);
      
      setOperations([]);
      return results;
    } catch (error) {
      console.error('Batch processing failed:', error);
      throw error;
    } finally {
      setIsProcessing(false);
    }
  }, [operations, isProcessing]);

  const clearBatch = useCallback(() => {
    setOperations([]);
  }, []);

  return {
    operations,
    addOperation,
    processBatch,
    clearBatch,
    isProcessing,
    batchSize: operations.length
  };
};

export default {
  AdminDataSyncManager,
  adminSyncManager,
  useOptimizedAdminState,
  useMemoizedTransform,
  useBackgroundDataFetcher,
  useOptimizedSearch,
  useVirtualizedList,
  useAdminPerformanceMonitor,
  useOptimizedModal,
  useSmartCache,
  useBatchOperations
};
